s, RCA-Victor, BMG, e PortugalSom. Autor, tem cerca de\n          uma dezena de livros publicados. Venceu o Prémio Matilde Rosa Araújo\n          de Conto Infantil (2010) com Amilcar, consertador de Búzios Calados\n          e o Prémio Bocage de Conto (2010) com Afonso Cabrita, meu tio,\n          ensaísta, toureiro e melancólico. Escreveu os libretos das óperas\n          “Os dilemas dietéticos de uma Matrioska do meio” com música de Nuno\n          Côrte-Real “As sete mulheres de Jeremias Epicentro” com música de\n          Jorge Prendas (Quarteto Contratempus) e “Tic-Tac Punfffff...” com\n          música de Telmo Marques (Casa da Música). Estreou no Teatro Nacional\n          de São Carlos “A Verdadeira Hidrostória do Elefonte” com música de\n          Sofia Sousa Rocha (2018). Encenou as óperas “Pimpinone” de Telemann,\n          “Bastien e Bastienne” e “Livietta e Tracollo” de Pergolesi e “La\n          Canterina” de Haydn (Ass. Ritornello), “Hansel e Gretel” de\n          Humperdinck (Temporada Darcos), “Amahl e os Visitantes da Noite”\n          (CCVilaFlor – Guimarães),“O Limpa-Chaminés” de Britten (EMVP, Gaia).\n          Co-fundou o quinteto vocal Vozes da Rádio e a companhia Opera Isto.\n          Dirige o Estúdio de Ópera do CMC e o Atelier de Ópera da ESART.\n        ")])])],1)],1)],1)}
